import React, { Component } from 'react';

class Item extends Component {
    render() {
        var { value } = this.props;
        return (
            <React.Fragment>
                <div className={`col-md-${value.position} animate`} data-animate-effect="fadeIn"><a className="img-bg" href={ value.href } target="_blank" style={{ backgroundImage: `url(" ${ value.img } ")`}}>
                    <div className="photo-details">
                        <h2>{value.title}</h2>
                    </div></a></div>
            </React.Fragment>
        );
    }
}

export default Item;