import React, { Component } from 'react';
import data from '../data.json';
import Item from './Item';

class Items extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="row gutter16">
                    { this.showItem(data) }
                </div>

                <div className="row">
                    <div className="text-center mt50"><a className="btn btn-primary" href="assets/fonts/CV-Ngo-Ngoc.pdf" download>Download My CV</a></div>
                </div>
            </React.Fragment>
        );
    }
    showItem = (data) => {
        var result = null;
        if (data.length > 0) {
            result = data.map((value, index) => {
                return <Item
                    key={index}
                    value={value}
                />
            })
        }
        return result;
    }
}

export default Items;